		Loadthepage(e) {
			console.log(this.loadshow,22222)
			var self = this;
			if(!self.loadshow){
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				self.$service.auth(self , self.$service.api.index.comment_list , "POST" , {
					page:page,
					limit:15,
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.page_total;
					self.page = page;
					var list = res.data.data;
					list.forEach((v, i) => {
						self.evallis.push(v);
						self.$set(self.evallis[i] , 'stars' , self.stars);
					});
					console.log(self.evallis , 'evallis');
					if (self.pages == self.page) {
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},

	},
